.form-container{
    width: 500px;
    height: 600px;
    margin: 20px auto;
    border: 1px solid #95A0A7;
    border-radius: 8px;
   
   
}
.form-title{
    font-weight: 400;
    font-size: 35px;
    line-height: 42px;
    letter-spacing: 0.0025em;
    color: #2A414F;
    text-align: center;
}
.form-control{
    margin-left: 40px;
    margin-bottom: 20px;
}
.form-control label{
    display: block;
    font-weight: 400;
    font-size: 17px;
    line-height: 20px;
    letter-spacing: 0.0015em;
    margin-bottom: 10px;
    color:#2A414F;
}
.form-control input{
    width: 415px;
    height: 55px;
    border: 1px solid #95A0A7;
    border-radius: 5px;
    padding-left: 10px;
}
.btn-submit{
    width: 415px;
    height: 55px;
    background: rgba(255, 153, 0, 0.3);
    margin-top: 45px;
    border-radius: 5px;
    font-size: 21px;
    font-weight: 400;
    letter-spacing: 0.0015em;
    color: #0E161A;
    border: none;
   
     margin-left: 45px;
}
.login{
    margin-left: 45px;
}